                <span @click="tijiaoShiming()">提交</span>
            </div>
        </div>
        <div class="l-shangchuan" v-if="tupian">
            <div @click="v_hide()" class="vevk"></div>
            <div class="pOrC">
                <div class="tishis">
                    <img src="../../../../static/images/uiliID.png"
                         v-if="tiui == '1'" class="tiui1">
                    <img
