                        v-if="tiui == '2'" class="tiui2">
                </div>
                <span @click="xuanzeImg(0)" class="sp1">拍摄</span>
                <span @click="xuanzeImg(1)" v-if="!shouchi" class="sp2">手机相册选择</span>
                <span class="sp3" @touchstart="v_hide()">取消</span>
            </div>
