            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    .l-top-info-i {
        display: inline-block;
        width: 20px;
        height: 20px;
        background: url("../../../../static/images/dingwei.png") no-repeat center;
        background-size: 100% 100%;
    }

    .l-top-dingwei-i {
        display: inline-block;
        width: 80px;
        height: 40px;
        background: url("../../../../static/images/fanhuixiangyb.png") no-repeat left center;
        background-size: 0.2rem 0.36rem;
        position: relative;
        z-index: 103;
    }

    .l-index-top {
        .sp1 {
            color: #333;
            font-size: 16px;
            width: 80px;
            text-align: right;
            position: relative;
            z-index: 101;
        }
        width: 100%;
        height: 50px;
        padding: 0 15px;
        background: #51c1ea;
        display: flex;
        align-items: center;
        justify-content: space-between;
        box-sizing: border-box;
        position: fixed;
        top: 0;
        z-index: 102;
    }

    .address{
        position:absolute;
        top: 0px;
        bottom: 0px;
        left: 0px;
        right: 0px;
    }
    .address .addressbox{
        height: 40%;
        position: absolute;
        z-index: 101;
        width: 100%;
        max-height: 100%;
        overflow: hidden;
        background: #fff;
        bottom: 0px;
    }
    .address .addressbox .text_btn{
        padding: 0 0.4rem;
        box-sizing: border-box;
        height: 0.85rem;
        font-size: 0.28rem;
        background: #f7f7f7;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .addressSelect .selectbox{
        width: 100%;
        height: 26px;
        border-top: 1px solid #ccc;
        border-bottom: 1px solid #ccc;
        margin-top: 60px;
        background: #F9F9F9;
    }
    .address .addressboxbg{
        position: absolute;
        left: 0;
        top: 0;
        z-index: 100;
        width: 100%;
        height: 100%;
        background: rgba(0,0,0,.7);
    }
    .addressSelect{width: 100%; position: relative; background: #fff; height: 190px;overflow: hidden; -webkit-mask-box-image: linear-gradient(0deg,transparent,transparent 5%,#fff 20%,#fff 80%,transparent 95%,transparent); font-size: 14px;}
    .addressSelect ul{width: 100%; position: absolute; left: 0; top:60px; -webkit-transform-style: preserve-3d; -webkit-backface-visibility:hidden; text-align: center; padding-left: 0;}
    .addressSelect ul li{
        white-space : nowrap;
        overflow: hidden; text-overflow:ellipsis;
        color: #999;
        font-size: 0.38rem;
        padding: 3px 0;}
    .addressSelect ul:nth-of-type(2){left: 33.333333%;}
    .addressSelect ul:nth-of-type(3){left: 66.666666%;}
    .addressSelect ul li.addSelectActive{
        font-size: 0.42rem;
        color: #333; transform: scale(1.1); transition: 0.5s;}
    .selectAni{transition: 0.8s;}
</style>



