<template>
    <div id="cf_loading" v-show="show" class="cf_loading">
        <div class="cf_center" style="width: 2.4rem;padding: 0.2rem;border-radius: 5px;">
            <div class="imgs" style="height: 2rem;overflow: hidden;">
                <img  style="width: 2rem;height: 2rem;float: left;" src="data:image/gif;base64," alt="">
            </div>
            <div class="title" style="width: 100%;">{{title}}</div>
        </div>
    </div>
</template>

<script>
    export default {
        data () {
            return {
                psd:'',//mima
                isRouterAlive: true,
                autofocus:'autofocus',
            }
        },
        watch:{
        },
        props:["show","title"],
        mounted:function () {
        },
        methods: {
        }
    }
</script>
<style lang="scss" scoped>
    //遮罩层
    .cf_loading{
        position: fixed;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        max-width: 7.5rem;
        margin: auto;
        background-color: rgba(0,0,0,0.5);
        z-index: 99999999;
        .cf_center{
            background-color: white;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translateX(-50%) translateY(-50%);
            .title{
                text-align: center;
                font-size: 0.26rem;
            }
        }
    }
</style>


