<template>
    <div class="bj">
        <div class="tanchuang">
            <div class="biaoti">请选择您的角色</div>
            <div class="zhuti">
                <div @click="imgClick('D')" class="i1">
                    <img src="../../../static/images/duanbo.png">
                    <span>同城运输</span>
                </div>
                <div @click="imgClick('C')" class="i2">
                    <img src="../../../static/images/changtu.png">
                    <span>长途运输</span>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data () {
        return {

        }
    },
    mounted:function () {
        this.__main()
    },
    computed:{

    },

    methods: {
        mssage(info, po) {
            this.$Toast({
                message: info,
                position: po,
            })
        },

        imgClick(i) {
            var th = this
            var data = JSON.parse(this.$route.query.data)
            data.roleId = i
            th.ajax.post('/driver/api/register', data)
            .then((r) => {
                if (r.resultCode === 0) {
                    th.mssage('注册成功', 'bottom')
                    localStorage.juese = i
                    setTimeout(function() {
                        th.$router.push('/login')
                    }, 1000)
                }
            })
        },

        __main() {
            var w = window.innerWidth
            document.querySelector('.tanchuang').style.height = (w * 0.7 * 1.3) + 'px'
        },
    }
}
</script>

<style lang="scss" scoped>
    body {
        display: flex;
        justify-content: center;
        overflow: hidden;
    }
    .bj {
        min-width: 375px;
        width: 100%;
        height: 100vh;
        background: url("../../../static/images/bj.png") no-repeat center;
        background-size: 100% 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .tanchuang {
        width: 70%;
        border-radius: 5px;
        background: #fff;
        padding-top: 65px;
        box-sizing: border-box;
        position: relative;
    }

    .biaoti {
        position: absolute;
        top: 0;
        width: 100%;
        height: 65px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-bottom: solid 1px #ddd;
        color: #333;
        font-size: 16px;
    }

    .zhuti {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }

    .i1 {
        width: 30%;
        margin-left: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 15px;
    }

    .i2 {
        width: 30%;
        margin-right: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 15px;
    }

    .zhuti img {
        width: 100%;
    }

    .zhuti span {
        color: #666;
        font-size: 14px;
        margin-top: 15px;
    }
</style>



